@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'FuturaPTMedium';
  src: url("../fonts/FuturaPTMedium.eot");
  src: local("☺"), url("../fonts/FuturaPTMedium.woff") format("woff"), url("../fonts/FuturaPTMedium.ttf") format("truetype"), url("../fonts/FuturaPTMedium.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaPTHeavy';
  src: url("../fonts/FuturaPTHeavy.eot");
  src: local("☺"), url("../fonts/FuturaPTHeavy.woff") format("woff"), url("../fonts/FuturaPTHeavy.ttf") format("truetype"), url("../fonts/FuturaPTHeavy.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaPTDemi';
  src: url("../fonts/FuturaPTDemi.eot");
  src: local("☺"), url("../fonts/FuturaPTDemi.woff") format("woff"), url("../fonts/FuturaPTDemi.ttf") format("truetype"), url("../fonts/FuturaPTDemi.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

body {
  color: #3d3c3d;
}

.wrapper {
  margin: 0 auto;
  max-width: 1250px;
  /*border: 1px solid red;*/
}

.button {
  width: 198px;
  height: 42px;
  border-radius: 5px;
  background-color: #ffcf4b;
  font-family: 'FuturaPTDemi';
  font-size: 19px;
  padding: 7px 0 0 27px;
  letter-spacing: 0.0287em;
}

.line {
  display: flex;
  height: 6px;
  margin-top: 16px;
}

.line__part1 {
  width: 835px;
  background: #ffd04b;
  position: relative;
}

.line__part1:before {
  position: absolute;
  content: '';
  height: 6px;
  background: #ffd04b;
  width: 2000px;
  right: 100%;
}

.line__part2 {
  width: 415px;
  background: #faa61a;
  position: relative;
}

.line__part2:after {
  position: absolute;
  content: "";
  height: 6px;
  background: #faa61a;
  width: 2000px;
  left: 100%;
}

.header-top {
  display: flex;
  padding-top: 16px;
}

.header-top__content {
  font-family: 'FuturaPTMedium';
  font-size: 22px;
  margin: 4px 0 0 104px;
  letter-spacing: 0.03em;
  /*white-space: nowrap;*/
}

.header-top__content:nth-child(1) {
  margin: 4px 42px 0 -2px;
}

.header-top__content span {
  font-family: 'FuturaPTHeavy';
}

.header-bottom {
  display: flex;
  height: 43px;
  margin-top: 45px;
  /*border: 1px solid red;*/
}

.menu {
  display: flex;
  margin: 10px 0 0 68px;
}

.menu__item {
  margin-right: 1.645em;
}

.menu__link {
  text-decoration: none;
  font-family: 'FuturaPTMedium';
  font-size: 20px;
  color: #3d3c3d;
  letter-spacing: 0.03em;
}

.search {
  width: 22px;
  height: 22px;
  background-image: url("../img/search.png");
  margin: 13px 0 0 14px;
}

.language {
  display: flex;
  justify-content: space-between;
  margin: 8px 0 0 55px;
  width: 105px;
}

.language__item {
  font-family: 'FuturaPTMedium';
  font-size: 22px;
  letter-spacing: 0.03em;
  position: relative;
}

.language__item:before {
  position: absolute;
  content: '';
  width: 2px;
  height: 20px;
  background: #3d3c3d;
  top: 6px;
  left: -21px;
}

.language__item_main {
  color: #77b8e6;
}
